import React from 'react'
import './RecommendedAds.scss'
import { Link } from 'react-router-dom'
import { getRecommendedAds } from '../../redux/selectors/adsSelector'
import { useSelector } from 'react-redux'
import config from '../../config/default.json'
import logo from '../../assets/png/logo.png'

export default function RecommendedAds() {
  const recommendedAds = useSelector(getRecommendedAds)

  return (
    <div className='RecOffers' >
      <h2 className='RecOffers__title'>
        Рекомендованные
        <svg width="25" height="25" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink">
          <mask id="mask0" mask-type="alpha" maskUnits="userSpaceOnUse" x="0" y="0" width="22" height="24">
            <rect width="22" height="24" fill="url(#pattern0)" />
          </mask>
          <g mask="url(#mask0)">
            <rect x="1" y="-3" width="24" height="28" fill="url(#pattern1)" />
            <rect x="1" y="-3" width="24" height="28" fill="white" />
          </g>
          <defs>
            <pattern id="pattern0" patternContentUnits="objectBoundingBox" width="1" height="1">
              <use xlinkHref="#image0" transform="translate(-0.0454545) scale(0.00213068 0.00195312)" />
            </pattern>
            <pattern id="pattern1" patternContentUnits="objectBoundingBox" width="1" height="1">
              <use xlinkHref="#image0" transform="translate(-0.0833333) scale(0.00227865 0.00195312)" />
            </pattern>
            <image id="image0" width="512" height="512" xlinkHref="data:image/png;base64," />
          </defs>
        </svg>

      </h2>
      <ul className='RecOffers__list'>
        {
          recommendedAds.map((ad, i) => (
            <li key={i} className='RecOffers__item'>
              <h3 className='RecOffers__item-title'>{ad.title}</h3>
              <div className="RecOffers__item-content">
                <p className="RecOffers__item-description">
                  {ad.description}
                </p>
                <img className="RecOffers__item-img" src={!ad.img[0] ? logo : `${config.serverUrl}/api/images/${ad.img[0]}`} />
              </div>
              <div className="RecOffers__item-link">
                <Link to={`/detailsAd/${ad._id}`}><span className="RecOffers__link-text">Подробнее</span></Link>
              </div>
            </li>
          ))
        }
      </ul>
    </div>
  )
}
